.currency-converter-container{
    font-size: 1.6rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    height: 100%;
    width: 100%;
}

.component-title{
    padding: 2px 4px;
    font-size: 2.4rem;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-weight: bold;
    margin: 8px 12px 20px;
    text-decoration: underline var(--orange) 4px;
    border-radius: 12px;
    text-align: center;
}

.currency-main-container{
    margin: 0px 12.4px;
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#currency-input-focused{
    outline: solid var(--orange) 2.4px;
}

.convert-container{
    margin: 4px;
    background-color: var(--white);
    display: flex;
    color: var(--black);
    width: 90%;
    height: 2rem;
    display: flex;
    font-size: 1.6rem;
    border-radius: 8px;
    outline: solid var(--white) 2.4px;
}

.convert-container > * {
    margin: 5.2px;
}

.currency-input-label{
    margin: 0px 8px;
    font-size: 1rem;
    color: var(--lightgray);
    width: 90%;
}

.currency-field{
    width: 95%;
    font-size: 1.2rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    background-color: transparent;
    border: none;
}

.currency-dropdown-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.currency-dropdown{
    appearance: none;
    background-color: transparent;
    font-family: 'Courier New', Courier, monospace;
    width:2.2rem;
    height: fit-content;
    border: none;
    border-radius: 12px;
    font-weight: bold;
}

*:focus{
    outline: none;
}

optgroup{
    font-style: normal;
    font-weight: bolder;
}

.currency-dropdown:focus{
    outline: solid 2px var(--orange);
}

.convert-icon-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 16px;
}

.crypto-checkbox-container{
    margin: 20px 4px 8px 4px;
    display: flex;
    justify-content: right;
    align-items: center;
    width: 90%;
}


#crypto-inclusion{
    position: absolute;
    visibility: hidden;
}

.crypto-inclusion-label{
    padding: 4px;
    font-weight: bold;
    font-size: 1rem;
    color: var(--orange);
    border: 2px solid var(--orange);
    border-radius: 16px;
    transition: color 0.4s ease-out;
}

.crypto-inclusion-label:hover{
    border: 2px solid var(--gray1);
    transition: border 0.4s ease-out;
}

.crypto-inclusion-label:active{
    background-color: var(--gray2);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.crypto-inclusion-off{
    color:var(--gray2);
    border: 2px solid var(--gray2);
    transition: color 0.4s ease-out;
}

.currency-exchange-rate{
    color: var(--orange);
}

.currency-exchange-rate-date{
    font-size: 0.8rem;
    margin: 2.4px;
}