.linux-permissions-container{
    font-size: 1.6rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    height: 100%;
    width: 100%;
}

.permissions-main-container{
    display: flex;
    flex: 1 1 1;
    justify-content: space-around;
    width: 100%;
}

.sub-container{
    display: flex;
    flex-direction: column;
    flex: 1 1 1;
    justify-content: space-around;
}
.sub-container > * {
    padding: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.permissions-title{
    font-weight: bold;
}

.permissions-result-container{
    display: flex;
    justify-content: space-around;
}

.linux-permissions-value{
    margin-top: 8px;
    font-size: 22px;
    font-weight: bolder;
    font-family: 'Courier New';
    text-align: center;
}

.linux-permission-label{
    text-align: center;
    color: var(--orange);
}

.permissions-hidden-checkbox{
    visibility: hidden;
    position: absolute;
}

.permission-granted{
    color: var(--orange);
    cursor: pointer;
}

.no-permission{
    color: var(--gray3);
    cursor: pointer;
}

.command-container{
    background-color: var(--gray2);
    display: flex;
    margin: 12px 16px 8px;
    padding: 8px;
    font-size: 1rem;
    font-family: 'Courier New', Courier, monospace;
    width: 90%;
    justify-content: space-between;
    align-items: center;
    border-radius: 8px;
}

.command-container:hover{
    background-color: var(--gray1);
    cursor: pointer;
}

.command-container:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}