.home-container{
    font-size: 1.36rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    height: 100%;
}

.home-title{
    text-align: center;
    font-size: 3.2rem;
    box-sizing: border-box;
}

.namesake{
    color: var(--orange2);
    margin: 0px 4px;

}

.tool-descriptor-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
    box-sizing: border-box;
}

.tool-descriptor{
    min-width: 160px;
    max-width: 400px;
    border: 2px solid var(--gray2);
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    flex-basis: 31.6%;
    align-items: center;
    justify-content: center;
    margin: 8px 2px;
    aspect-ratio: 1/1;
    background-color: var(--gray1);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.tool-descriptor:hover{
    background-color: var(--orange);
    border: 2px solid var(--orange2);
    box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

.tool-descriptor:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.author-descriptor{
    background-color: var(--black);
    border: 2px solid var(--orange);
    color: var(--orange);
}

.author-descriptor:hover{
    background-color: var(--gray1);
    color: var(--orange2);
    box-shadow: rgba(253, 123, 58, 0.25) 0px 54px 55px, rgba(253, 123, 58, 0.12) 0px -12px 30px, rgba(253, 123, 580, 0.12) 0px 4px 6px, rgba(253, 123, 58, 0.17) 0px 12px 13px, rgba(253, 123, 58, 0.09) 0px -3px 5px;
}

.author-descriptor:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.tool-icon-container{
    margin: 4px;
}

.tool-name{
    margin: 4px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-weight: bold;
    text-align: center;
}

.tool-description{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    text-align: center;
    margin: 4px;
    font-size: 0.6rem;
    font-weight: 200;
    font-style: italic;
}

.property-calculator-descriptor:hover{
    color: var(--lightgray);
    background-color: #6C8FB8;
    border-color: #3C5066;
}