.color-converter-container{
    font-size: 1.6rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    height: 100%;
}

.color-model-container{
    display: flex;
    flex-direction: column;
}

.color-input-label{
    font-size: 1rem;
}

.main-input-container{
    width: 100%;
    display: flex;
    flex-direction: row;
    margin: 8px 0px;
    align-items: center;
}

.single-input-container{
    display: flex;
    flex-direction: row;
    width: fit-content;
    height: fit-content;
    background-color: var(--lightgray);
    border: 2px solid var(--lightgray);
    border-radius: 8px;
}

.color-input-focused{
    border: 2px solid var(--orange);
}

.hash-label{
    font-size: 1.3rem;
    font-weight: bold;
    font-style: normal;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    width: 5%;
    aspect-ratio: 1 / 1;
    display:flex;
    align-items: center;
    justify-content: right;
    background-color: transparent;
    color: var(--gray3);
    transition: color 0.18s linear;
    margin-left: 4px;
    border: 1.2px solid var(--lightgray);
    border-radius: 6px;
}

.hash-label:hover{
    cursor: pointer;
    border: 1.2px solid var(--orange) ;
}

.hash-included{
    color: var(--black);
    transition: color 0.18s linear;
}

.color-input-single{
    margin: 0px 12px 0px 0px;
    font-size: 1.2rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-radius: 8px;
    background-color: var(--lightgray);
    border: 2px solid var(--lightgray);
    width: 90%;
}

.color-copy-button-container{
    border: solid blue;
}

.color-copy-btn{
    width: fit-content;
    height: fit-content;
    margin: 8px;
    color: var(--lightgray);
    background-color: var(--orange);
    border: 2px solid var(--orange);
    border-radius: 8px;
    box-shadow: rgba(253, 123, 58, 0.25) 0px 54px 55px, rgba(253, 123, 58, 0.12) 0px -12px 30px, rgba(253, 123, 580, 0.12) 0px 4px 6px, rgba(253, 123, 58, 0.17) 0px 12px 13px, rgba(253, 123, 58, 0.09) 0px -3px 5px
}

.color-copy-btn:hover{
    background-color: var(--orange2);
    border: 2px solid var(--orange2);
}

.color-copy-btn:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    border-color: rgba(50, 50, 93, 0.2);
}

.multi-input-container{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    flex: 1 1 1;
}

.color-input-triple{
    margin: 4px 12px 4px 0px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 1.2rem;
    border: 2px solid var(--lightgray);
    background-color: var(--lightgray);
    border-radius: 8px;
    width: 2.4rem;
    min-width: 2rem;
}

.color-input-triple:focus{
    border: 2px solid var(--orange);
}

.color-input-quadruple{
    margin: 4px 8px 4px 0px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 1.2rem;
    border: 2px solid var(--lightgray);
    background-color: var(--lightgray);
    border-radius: 8px;
    width: 2rem;
    min-width: 1.6rem;
}

.color-input-quadruple:focus{
    border: 2px solid var(--orange);
}

.color-preview-container{
    margin-top: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1 1 1;
}

.color-preview-label{
    font-size: 0.88rem;
    margin: 4px;
    text-decoration: underline 1.2px var(--orange);
}

.color-preview{
    border-radius: 16px;
    color: transparent;
    width: 60%;
    aspect-ratio : 1 / 0.2;
    padding: 4px;
    border: 3.2px groove var(--lightgray);
    border-style: ridge;
}