.author-details-container{
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 1.52rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile-image{
    width: 180px;
    padding: 4px 8px;
    transition: filter 2s linear;
    -webkit-transition: filter 2s ease-in;
}

.profile-image:hover{
    width: 180px;
    padding: 4px 8px;
    border-radius: 128px;
    filter: invert(77%) sepia(50%) saturate(4675%) hue-rotate(328deg) brightness(101%) contrast(101%);
    transition: filter 4s ease-in, background-color 2s linear;
    -webkit-transition: filter 4s ease-in;
}

.author-pfp-image-container{
    background-color: var(--lightgray);
    border-radius: 128px;
    border: 4px solid var(--black);
    width: fit-content;
    height: fit-content;
    transition: filter 2s linear, background-color 0.4s ease-in;
    -webkit-transition: filter 2s ease-in, background-color 0.4s ease-in;

}

.author-pfp-image-container:hover{
    background-color: transparent;
    border: 4px solid var(--orange2);
    transition: border 6.4s linear, background-color 2s linear;
    -webkit-transition: border 6.4s ease-out, background-color 0.4s ease-in;

    
}

.author-name{
    font-size: 1.6rem;
    margin: 8px;
    cursor: pointer;
}

.miniaturized-spacing{
    font-size: 0.88rem;
}

.shown-title{
    margin: 0px 2px;
    font-size: 1.4rem;
    color: var(--orange);
    transition: margin 0.44s linear, font-size 1.2s ease-out;
    -webkit-transition: margin 0.44s linear, font-size 1.2s ease-out;
}

.hidden-title{
    margin: 0px;
    font-size: 0;
    color: var(--orange);
    transition: margin 0.44s linear, font-size 1.2s ease-out;
    -webkit-transition: margin 0.44s linear, font-size 1.2s ease-out;
}

#uncolored{
    color: var(--lightgray);
}

