.external-icon-container{
    margin: 2px 5.2px;
    width: fit-content;
    aspect-ratio: 1/1;
    border: 2px solid var(--orange2);
    border-radius: 32px;
    background-color: var(--orange2);
}

.external-icon-container > *{
    padding: 6.4px;
}

.external-link-container{
    display: flex;
    margin: 4px;
    border: none;
    border-radius: 24px;
    align-items: center;
    background-color: var(--orange);
}

.author-external-link{
    color: var(--lightgray);
    text-decoration: underline solid deepskyblue;
}

.author-external-link:visited{
    color: var(--lightgray);
    text-decoration: underline solid purple;
}

.external-link-label{
    font-size: 1rem;
    margin: 4px 12px 4px 4px;
    transition: margin 0.44s linear, font-size 1.6s linear;
    -webkit-transition: margin 0.12s linear, font-size 0.24s linear;
}

.external-link-label-hidden{
    font-size: 0px;
    margin: 0px;
    transition: margin 0.44s linear, font-size 1.6s linear;
    -webkit-transition: margin 0.12s linear, font-size 0.24s linear;
}

.return-to-home{
    border: 2px solid var(--orange);
    border-radius: 12px;
    padding: 4px 8px;
    margin: 12px;
    font-size: 1.2rem;
    color: var(--orange);
    cursor: pointer;
    transition: color 0.24s linear, background-color 0.24s linear;
    -webkit-transition: color 0.24s linear, background-color 0.24s linear;
}

.return-to-home:hover{
    background-color: var(--orange2);
    color: var(--lightgray);
    transition: color 0.24s linear, background-color 0.24s linear;
    -webkit-transition: color 0.24s linear, background-color 0.24s linear;
}

.return-to-home:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}