.color-base-converter-container{
    font-size: 1.6rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    height: 100%;
}

.color-base-converter-title{
    text-decoration: none;
    margin-top: 8px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.converter-selection{
    margin: 4px;
    padding: 0px 4px 2.4px;
    border: solid 3.2px var(--orange);
    border-radius: 14px;
    background-color: transparent;
    color: var(--orange);
    transition: color 0.24s linear, background-color 0.32s ease;
}

.converter-selected{
    color: var(--lightgray);
    background-color: var(--orange);
    transition: color 0.4s linear, background-color 0.4s ease;
}

.converter-selection:hover{
    color: var(--white);
    background-color: var(--orange2);
    border-color: var(--orange2);
    cursor: pointer;
}