.card-analyzer-container{
    font-size: 1rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    height: 100%;
    display: flex;
    flex-direction: column;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.card-analyzer-container > *{
    margin: 4px 8px;
}

.card-input-container{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.card-number-input{
    font-size: 1.2rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-radius: 8px;
    background-color: var(--lightgray);
    border: 2px solid var(--lightgray);
}

.card-number-input:focus{
    border: 2px solid var(--orange);
}

.card-number-copy-btn{
    width: fit-content;
    height: fit-content;
    margin: 8px;
    color: var(--lightgray);
    background-color: var(--orange);
    border: 2px solid var(--orange);
    border-radius: 8px;
    box-shadow: rgba(253, 123, 58, 0.25) 0px 54px 55px, rgba(253, 123, 58, 0.12) 0px -12px 30px, rgba(253, 123, 580, 0.12) 0px 4px 6px, rgba(253, 123, 58, 0.17) 0px 12px 13px, rgba(253, 123, 58, 0.09) 0px -3px 5px
}

.card-number-copy-btn:hover{
    background-color: var(--orange2);
    border: 2px solid var(--orange2);
}

.card-number-copy-btn:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    border-color: rgba(50, 50, 93, 0.2);
}

.card-sub-container{
    display: flex;
    flex-direction: row;
}

.card-info-label{
    margin: 4px;
}

.card-info-value{
    font-weight: 300;
    margin: 4px;
    color: var(--gray3);
    -webkit-transition: color 1.6s ease-out, font-weight 0.12s ease-out;
    transition: color 1.6s ease-out, font-weight 0.12s ease-out;
}

.card-info-valid{
    color: var(--orange);
    font-weight: bold;
    -webkit-transition: color 1.6s ease-out, font-weight 0.12s ease-out;
    transition: color 1.6s ease-out, font-weight 0.12s ease-out;
}

.card-preview-label{
    margin-top: 12px;
    font-size: 0.88rem;
    text-align: center;
    text-decoration: underline 2px var(--orange);
}

.virtual-card-representation{
    aspect-ratio: 85.6/53.98;
    border-radius: 6px ;
    margin: 12px 2px 2px 2px;
    max-width: 20rem;
    width: 100vw;
    background-color: var(--gray2);
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
    -webkit-transition: background-color 1.6s ease-out;
    transition: background-color 1.6s ease-out, animation 0.8s ease-out;
    animation: none;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 2px 1px, rgba(0, 0, 0, 0.09) 0px 4px 2px, rgba(0, 0, 0, 0.09) 0px 8px 4px, rgba(0, 0, 0, 0.09) 0px 16px 8px, rgba(0, 0, 0, 0.09) 0px 32px 16px;box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
}

.virtual-card-representation-valid{
    background-color: var(--orange2);
    -webkit-transition: background-color 1.6s ease-out;
    transition: background-color 1.6s ease-out;
}

.black-bar-representation{
    width: 100%;
    aspect-ratio: 10/1;
    background-color: black;
    margin-top: 8px;
}

.card-number-representation{
    font-weight: bold;
    font-family: Verdana, Geneva, Tahoma, sans-serif, Courier, monospace;
    font-size: 1.2rem;
    text-align: center;
    margin: 8px;
}


.card-classified-info{
    font-size: 0.88rem;
    font-family: 'Courier New', Courier, monospace;
    font-weight: bold;
    margin: 4px;
}

.card-network-representation{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: right;
    margin: 24px 10px 0px;
}

@keyframes rotate-y-axis{
    0% {
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
    }
    50% {
        -webkit-transform: rotateY(360deg);
        transform: rotateY(360deg);
    }
    100% {
        -webkit-transform: rotateY(0deg);
        transform: rotateY(0deg);
    }
}

.virtual-card-representation:hover{
    -webkit-animation: rotate-y-axis 8s normal linear infinite;
    animation: rotate-y-axis 8s normal linear infinite;
    cursor: pointer;
}