.markdown-formatter-container{
  font-size: 1.6rem;
  border-radius: 12px;
  margin: 4px;
  padding: 4px 12px;
  /* height: 100%; */
  height: fit-content;
  width: 100%;
  /* overflow-y: auto; */
}

.box-container{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

/* .box-container > *{
  flex-basis: 46%;
} */

.input-container{
  border: none;
  border-radius: 6px;
  width: fit-content;
  margin: 4px;
}

.unformatted-markdown-input{
  font-size: 0.9rem;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
  border: 4px solid transparent;
  border-radius: 6px;
  resize: none;
  margin: 8px;
  min-width: 355px;
  max-width: 890px;
  padding: 4px;
  min-height: 250px;
  height: 69vh;
  max-height: 800px;
  overflow-y: auto;
  flex-basis: 44%;
}

.unformatted-markdown-input:focus{
  border: 4px solid var(--orange);
}

.preview-container{
  color: var(--black);
  background-color: var(--lightgray);
  border: 4px solid var(--gray3);
  border-radius: 8px;;
  font-size: 1rem;
  min-width: 360px;
  max-width: 900px;
  margin: 6.8px;
  min-height: 250px;
  height: 70vh;
  max-height: 800px;
  overflow-y: auto;
  word-wrap: break-word;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
  flex-basis: 46%;
}

.preview-container > *{
  margin: 2px 4px;
  
}

.markdown-btn-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

#markdown-btn{
  padding: 8px 12px;
  border: none;
  border-radius: 8px;
  margin: 4px;
  color: var(--white);
  background-color: var(--orange);
  font-size: 0.92rem;
  box-shadow: rgba(253, 123, 58, 0.25) 0px 54px 55px, rgba(253, 123, 58, 0.12) 0px -12px 30px, rgba(253, 123, 580, 0.12) 0px 4px 6px, rgba(253, 123, 58, 0.17) 0px 12px 13px, rgba(253, 123, 58, 0.09) 0px -3px 5px;
}

#markdown-btn:hover{
  background-color: var(--orange2);
}

#markdown-btn:active{
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.markdown-icon{
  margin: 0px 2px;
}

.markdown-tooltip{
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
  font-size: 1.12rem;
  margin: 6.4px;
  
  transition: font-size 0.4s ease;
}

.markdown-tooltip-hidden{
  font-size: 0px;
  margin: 0px;
  transition: font-size 0.4s ease;
}

.clear-btn-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: right;
}

a{
  color: deepskyblue;
  text-decoration: none;
}

a:visited{
  color: purple;
}