.base-converter-container{
    font-size: 1.6rem;
    border-radius: 12px;
    margin: 4px;
    padding: 4px 12px;
    height: 100%;
}

.value-input-container{
    display: flex;
    flex-direction: column;
}

.value-input-container> *{
    margin: 4px 8px;
}

.input-label{
    font-size: 1rem;
}

.base-subscript-label{
    font-size: 0.52rem;
    margin: 2.4px;
}

.value-input{
    font-size: 1.2rem;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif, Cochin, Georgia, Times, 'Times New Roman', serif;
    border-radius: 8px;
    background-color: var(--lightgray);
    border: 2px solid var(--lightgray);
    padding: 4px;
    width: 85%;
}

.value-input:focus{
    border: 2px solid var(--orange);
}

.base-input-container{
    display: flex;
    justify-content: space-between;
}

.base-value-copy-btn{
    width: fit-content;
    height: fit-content;
    margin: 8px;
    color: var(--lightgray);
    background-color: var(--orange);
    border: 2px solid var(--orange);
    border-radius: 8px;
    box-shadow: rgba(253, 123, 58, 0.25) 0px 54px 55px, rgba(253, 123, 58, 0.12) 0px -12px 30px, rgba(253, 123, 580, 0.12) 0px 4px 6px, rgba(253, 123, 58, 0.17) 0px 12px 13px, rgba(253, 123, 58, 0.09) 0px -3px 5px
}

.base-value-copy-btn:hover{
    background-color: var(--orange2);
    border: 2px solid var(--orange2);
}

.base-value-copy-btn:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
    border-color: rgba(50, 50, 93, 0.25);
}

.base-converter-icon{
    padding: 2px;
}