.App{
    color: var(--white);
    width: fit-content;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    /* overflow-y: auto; */
    overflow: hidden;
}

.App-adaptable{
    height: fit-content;
}

.tool-container{
    background-color: var(--black);
    width: fit-content;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* overflow-y: auto; */
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;
    transition: background-color 0.2s linear, border-radius 0.12s linear;
    -webkit-transition: background-color 0.2s linear, border-radius 0.12s linear;
}

.no-container{
    border-radius: 2px;
    background-color: transparent;
    width: fit-content;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.2s linear, border-radius 8s linear;
    -webkit-transition: background-color 0.2s linear, border-radius 0.12s linear;
    height: 100%;
    overflow-y: auto;
    scrollbar-width: none;
    -ms-overflow-style: none;
    box-sizing: border-box;
}

nav{
    font-size: 1.2rem;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    opacity: 0.12;
    background-color: var(--gray1);
    box-shadow: rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 56px, rgba(17, 17, 26, 0.1) 0px 24px 80px;
    transition: opacity 0.52s;
    height: 64px;
}

nav:hover{
    opacity: 1.0;
    cursor: pointer;
    transition: opacity 0.2s;
}

.left-navigation{
    display: flex;
    height: 100%;
}



.left-navigation > * {
    display: flex;
    padding: 0px 6.4px;
    overflow: hidden;
    transition: background-color 0.2s;
    justify-content: center;
    align-items: center;
}

.left-navigation > *:hover{
    background-color: var(--orange);
    color: var(--white);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    transition: background-color width 0.52s;
}

.left-navigation > * > *{
    margin: 4px;
}

.left-navigation > * .tool-name{
    visibility: hidden;
    width: fit-content;
    max-width: 105px;
    background-color: var(--gray2);
    color: var(--white);
    text-align: center;
    border-radius: 12px;
    padding: 4px 8px;
    position: absolute;
    z-index: 2;
    top: 64px;
    font-weight: 200;
}

.left-navigation > *:hover .tool-name{
    visibility: visible;
    box-shadow: rgba(0, 0, 0, 0.3) 0px 19px 38px, rgba(0, 0, 0, 0.22) 0px 15px 12px;

}

.home-page{
    /* change font, size, bold and make sure color isnt changed on hover */
    background-color: transparent;
    color: var(--orange);
    font-family: 'Segoe UI', 'Gill Sans' ,Tahoma,  Verdana, sans-serif;
    font-weight: bolder;
    font-size: 1.52rem;
}

.home-page:hover{
    background-color: transparent;
    color: var(--gray3);
}

.right-navigation{
    margin: 4px;
}

.right-navigation > *{
    padding: 6.4px;
}

#component-selected{
    background-color: var(--black);
}

.pfp-image-container{
    border: 2px solid var(--gray1);
    height: fit-content;
    padding: 2px 4px;
    border-radius: 32px;
    background-color: transparent;
    transition: background-color 0.8s ease-out, border 1.6s ease-out;
    -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
}

.pfp-image-container:hover{
    border: 2px solid var(--orange2);
    background-color: var(--orange);
    transition: background-color 0.8s ease-out, border 0.8s ease-out;
    -webkit-transition: background-color 0.8s ease-out, border 0.8s ease-out;
    box-shadow: rgba(253, 123, 58, 0.25) 0px 54px 55px, rgba(253, 123, 58, 0.12) 0px -12px 30px, rgba(253, 123, 580, 0.12) 0px 4px 6px, rgba(253, 123, 58, 0.17) 0px 12px 13px, rgba(253, 123, 58, 0.09) 0px -3px 5px;
}

.pfp-image-container:active{
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}

.profile-pic{
    filter: invert(77%) sepia(50%) saturate(4675%) hue-rotate(328deg) brightness(101%) contrast(101%);
    width: 36px;
    transition: filter 2s linear;
    -webkit-transition: filter 2s linear;
}

.profile-pic-hovered{
    filter: invert(95%);
    transition: filter 0.8s linear;
    -webkit-transition: filter 0.8s linear;
}
